.reviews {
  padding: 14px 0 0;

  @include vp-1199 {
    padding: 21px 0 20px;
  }

  @include vp-767 {
    padding: 4px 0 22px;
  }
}

.reviews__container {
  margin: 0 auto;
  padding: 0 0 40px 112px;
  overflow: hidden;
  position: relative;

  @media (min-width: 1200px) and (max-width: 1439px) {
    padding: 0 40px 40px;
  }

  @include vp-1199 {
    margin: 0;
    padding: 0 45px 40px;
  }

  @include vp-767 {
    padding: 0 15px 15px;
    margin: 0;
    min-width: 290px;
    max-width: 100%;
  }
}

.reviews__cover {
  margin: 0 auto;
  max-width: 1440px;
  overflow: hidden;
  position: relative;

  h2 {
    position: relative;
    width: 542px;
    text-align: start;
    margin: 19px 0 25px 9px;

    @include vp-1199 {
      width: 394px;
      margin: 18px 0 27px;
    }

    @include vp-767 {
      width: 156px;
      padding: 0;
      margin: 20px 0 35px;
    }
  }
}

.reviews__cover [data-content]::before {
  position: absolute;
  content: attr(data-content);
  font-size: 55px;
  font-weight: 800;
  line-height: 55px;
  opacity: 0.03;
  top: -17px;

  @include vp-1199 {
    font-size: 40px;
    line-height: 40px;
    top: -12px;
  }

  @include vp-767 {
    top: -3px;
    left: 0;
    width: 100%;
    max-width: 190px;
    font-size: 26px;
    line-height: 26px;
  }
}

.reviews__swiper-prev {
  position: absolute;
  content: "";
  top: 5px;
  right: 161px;
  width: 37px;
  height: 35px;
  background-color: $color-navy-blue;
  z-index: 55;
  cursor: pointer;
  border: none;

  @media (min-width: 1200px) and (max-width: 1380px) {
    right: 17.8%;
  }

  @include vp-1199 {
    right: 42px;
    top: 2px;
  }

  @include vp-767 {
    top: 12px;
    right: 42px;
  }

  svg {
    display: block;
    margin: auto;
  }
}

.reviews__swiper-next {
  position: absolute;
  content: "";
  top: 5px;
  right: 120px;
  width: 37px;
  height: 35px;
  background-color: $color-navy-blue;
  z-index: 55;
  cursor: pointer;
  border: none;

  @media (min-width: 1200px) and (max-width: 1380px) {
    right: 14.1%;
  }

  @include vp-1199 {
    top: 2px;
    right: 1px;
  }

  @include vp-767 {
    top: 12px;
    right: 1px;
  }

  svg {
    display: block;
    margin: auto;
  }
}

.reviews__wrapper {
  blockquote {
    max-width: 790px;
    min-height: 293px;
    height: auto;
    margin: 0;
    padding: 20px 40px 20px 20px;
    display: flex;
    flex-wrap: no-wrap;
    box-shadow: 0 0 30px 0 rgba(28, 51, 116, 0.1);
    background-color: $color-default-white;

    @include vp-1199 {
      width: 560px;
      min-height: 277px;
      padding: 18px 10px 10px 19px;
    }

    @include vp-767 {
      width: 100%;
      min-height: 466px;
      flex-direction: column;
      gap: 16px;
      padding: 20px 19px;
    }
  }
}

.reviews__avatar {
  width: 258px;
  min-height: 253px;

  @include vp-1199 {
    width: 186px;
    height: 96%;
    min-height: unset;
  }

  @include vp-767 {
    width: 100%;
    min-height: 169px;
    height: unset;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.reviews__text {
  width: 61%;
  margin-left: 4.1%;
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 0 15px;

  @include vp-1199 {
    width: 57.35%;
    height: 95%;
    margin-left: 6%;
    padding: 2px 0 0;
  }

  @include vp-767 {
    margin: 0;
    width: 100%;
    height: 54%;
    gap: 20px;
    justify-content: flex-start;
  }
}

.reviews__review {
  p {
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    text-align: start;
    margin: 0;
    margin-bottom: 10px;

    @include vp-1199 {
      font-size: 12px;
      line-height: 18px;
      margin-bottom: 10px;
    }

    @include vp-767 {
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.reviews__autor {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  margin-top: -6px;
  gap: 10px;

  @include vp-1199 {
    gap: 7px;
    margin-top: -6px;
  }

  @include vp-767 {
    margin-top: unset;
    gap: 0;
  }

  cite {
    display: block;
    font-weight: 700;
    font-size: 13px;
    line-height: 13px;
    font-style: normal;
    text-align: start;
    margin: 0;

    @include vp-1199 {
      line-height: 1;
      letter-spacing: -0.66px;
      padding-top: 12px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 12px;
      letter-spacing: 0;
      padding: 0;
      margin-top: -4px;
      margin-bottom: 8px;
    }
  }

  span {
    font-size: 12px;
    line-height: 12px;
    font-weight: 300;
    text-align: start;
    margin: 0;
    padding: 0;
  }
}
